<template>
  <v-parallax src="@/assets/home/header/umzug_1920.webp" height="700">

    <v-row
      align="end"
      justify="center"
    >
      <v-col class="text-center" cols="12">
        <div class="d-flex flex-column container">
          <h1 class="display-1 header-font font-weight-bold mb-1">{{ $vuetify.lang.t('$vuetify.home.header.headline') }}</h1>
          <h4 class="headline header-font mb-3">{{ $vuetify.lang.t('$vuetify.home.header.subline') }}</h4>

          <div class="d-flex justify-center height-correction">
            <div class="d-flex flex-row">
              <v-autocomplete
                class="dropdown mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                :placeholder="$vuetify.lang.t('$vuetify.form.country')"
                :items="countries"
                v-model="selectedCountryFrom"
              ></v-autocomplete>
              
              <v-combobox
                class="postcode mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                @change="changePostcode(selectedPostcodeFrom, 'from')"
                :placeholder="$vuetify.lang.t('$vuetify.form.postcode')"
                :items="postcodes"
                v-model="selectedPostcodeFrom"
              ></v-combobox>

              <v-combobox
                class="location mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                @change="changeLocation(selectedLocationFrom, 'from')"
                :placeholder="$vuetify.lang.t('$vuetify.form.locationFrom')"
                :items="locations"
                v-model="selectedLocationFrom"
              ></v-combobox>
            </div>
          </div>

          <div class="d-flex justify-center height-correction">
            <div class="d-flex flex-row">
              <v-autocomplete
                class="dropdown mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                :placeholder="$vuetify.lang.t('$vuetify.form.country')"
                :items="countries"
                v-model="selectedCountryTo"
              ></v-autocomplete>

              <v-combobox
                class="postcode mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                @change="changePostcode(selectedPostcodeTo, 'to')"
                :placeholder="$vuetify.lang.t('$vuetify.form.postcode')"
                :items="postcodes"
                v-model="selectedPostcodeTo"
              ></v-combobox>

              <v-combobox
                class="location mr-2 v-text-field v-text-field--single-line v-text-field--solo v-text-field--is-booted v-text-field--enclosed v-select"
                hide-no-data
                hide-details
                hide-selected
                color="success"
                @change="changeLocation(selectedLocationTo, 'to')"
                :placeholder="$vuetify.lang.t('$vuetify.form.locationTo')"
                :items="locations"
                v-model="selectedLocationTo"
              ></v-combobox>
            </div>
          </div>

          <div class="d-flex justify-center">
            <v-btn raised color="success" class="fix-wrap" x-large @click="handleCreateMove">
              {{ $vuetify.lang.t('$vuetify.home.header.createBtn') }}
              <v-icon right class="ml-4 mr-1">mdi-truck-fast-outline</v-icon> 
            </v-btn>
          </div>
        </div>
      </v-col>
    </v-row>
  </v-parallax>
</template>

<script>
import swissPostcodes from '@/assets/home/postcodes/switzerland.json'

export default {
  name: 'home-header',
  data: () => ({
    selectedCountryFrom: 'CH',
    selectedPostcodeFrom: '',
    selectedLocationFrom: '',
    selectedCountryTo: 'CH',
    selectedPostcodeTo: '',
    selectedLocationTo: '',
    countries: [ 'CH', 'DE', 'AT' ],
    postcodesRaw: swissPostcodes
  }),
  methods: {
    handleCreateMove() {
      this.$emit('createMove', {
        selectedCountryFrom: this.selectedCountryFrom,
        selectedPostcodeFrom: this.selectedPostcodeFrom,
        selectedLocationFrom: this.selectedLocationFrom,
        selectedCountryTo: this.selectedCountryTo,
        selectedPostcodeTo: this.selectedPostcodeTo,
        selectedLocationTo: this.selectedLocationTo
      })
    },
    canIUseWebP() {
      return document.createElement('canvas').toDataURL('image/webp').indexOf('data:image/webp') == 0;
    },
    changePostcode(value, type) {
      const postcodesRaw = { ...this.postcodesRaw }
      let itemFind

      for (let item in postcodesRaw) {
        if (item === value) {
          itemFind = postcodesRaw[item]
        }
      }

      if (itemFind) {
        if (type === 'from') {
          this.selectedLocationFrom = itemFind
        } else {
          this.selectedLocationTo = itemFind
        }
      }
    },
    changeLocation(value, type) {
      const postcodesRaw = { ...this.postcodesRaw }
      let itemFind

      for (let item in postcodesRaw) {
        if (postcodesRaw[item] === value) {
          itemFind = item
        }
      }

      if (itemFind) {
        if (type === 'from') {
          this.selectedPostcodeFrom = itemFind
        } else {
          this.selectedPostcodeTo = itemFind
        }
      }
    }
  },
  computed: {
    postcodes() {
      let postcodes = [];
      const postcodesRaw = { ...this.postcodesRaw }

      for (let item in postcodesRaw) {
        postcodes.push(item)
      }

      return postcodes;
    },
    locations() {
      let locations = [];
      const postcodesRaw = { ...this.postcodesRaw }

      for (let item in postcodesRaw) {
        locations.push(postcodesRaw[item])
      }

      return locations;
    }
  }
}
</script>

