<template>
  <div>
    <v-dialog
      :value="show"
      max-width="950"
      @click:outside="$emit('close')"
    >
      <v-card>
        <v-card-title class="headline i18n word-break">{{ title }}</v-card-title>

        <div class="container">
          <div class="markdown" v-html="mdContent"/>
        </div>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            {{ $vuetify.lang.t('$vuetify.home.btnClose') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import marked from 'marked'

export default {
  name: 'markdown-content-dialog',
  props: {
    show: Boolean,
    title: String,
    content: String
  },
  computed: {
    mdContent() {
      return marked(this.content)
    }
  }
}
</script>

