<template>
  <div>

    <Toolbar :save="false"/>
    <Header @createMove="createMove" />

    <section class="wrapped">
      <Welcome />
    </section>

    <section>
      <Steps />
    </section>

    <section class="wrapped mt-8">
      <RegistrationInfo />
    </section>

    <ScrollTop @scrollTop="scrollTop"/>

    <Footer />

  </div>
</template>

<script>
// @ is an alias to /src
import Toolbar from '@/components/Toolbar'

import Header from '@/components/home/Header'
import Footer from '@/components/home/Footer'
import Welcome from '@/components/home/Welcome'
import Steps from '@/components/home/Steps'
import RegistrationInfo from '@/components/home/RegistrationInfo'
import ScrollTop from '@/components/home/ScrollTop'

import { mapActions } from 'vuex'

export default {
  name: 'home',
  components: {
    Header,
    Footer,
    Toolbar,
    Welcome,
    Steps,
    RegistrationInfo,
    ScrollTop
  },
  data: () => ({
    
  }),
  methods: {
    ...mapActions([
      'createNewMove',
      'createLocalMove'
    ]),
    async createMove(event) {
      const { selectedPostcodeFrom, selectedLocationFrom, selectedPostcodeTo, selectedLocationTo } = event

      const change = [
        {
          path: 'move.addresses.currentAddress.postcode',
          value: selectedPostcodeFrom
        },
        {
          path: 'move.addresses.currentAddress.location',
          value: selectedLocationFrom
        },
        {
          path: 'move.addresses.newAddress.postcode',
          value: selectedPostcodeTo
        },
        {
          path: 'move.addresses.newAddress.location',
          value: selectedLocationTo
        }
      ]

      await this.createNewMove(change)
      await this.createLocalMove()

      // TODO: IF AUTHORIZED, ALSO SAVE ON SERVER

      this.$router.push({ name: 'move', params: { uuid: this.$store.getters.uuid } })
    },
    scrollTop() {
      window.scrollTo({top: 0, behavior: 'smooth'})
    }
  },
  mounted() {
    setTimeout(() => {
      this.scrollTop()
    }, 1)
  }
}
</script>

