<template>
  <div>
    <v-footer>
      <v-btn small text @click="showDisclaimer()">{{ $vuetify.lang.t('$vuetify.home.footer.disclaimer') }}</v-btn>
      <v-btn small text @click="showImprint()">{{ $vuetify.lang.t('$vuetify.home.footer.imprint') }}</v-btn>
      <v-btn small text @click="showContact()">{{ $vuetify.lang.t('$vuetify.home.footer.contact') }}</v-btn>
      <v-spacer></v-spacer>
      <div class="pl-4">&copy; {{ new Date().getFullYear() }}</div>
    </v-footer>

    <MDContentDialog :show="showMDDialog" @close="_toggleMDDialog()" :title="title" :content="content"/>
  </div>
</template>

<script>
import MDContentDialog from '@/components/dialog/MDContentDialog'

export default {
  name: 'home-footer',
  components: {
    MDContentDialog
  },
  data: () => ({
    showMDDialog: false,
    title: '',
    content: ''
  }),
  methods: {
    _toggleMDDialog() {
      this.showMDDialog = !this.showMDDialog
    },
    showDisclaimer() {
      this.title = this.$vuetify.lang.t('$vuetify.home.disclaimer.title')
      this.content = this.$vuetify.lang.t('$vuetify.home.disclaimer.mdContent')
      
      this._toggleMDDialog()
    },
    showImprint() {
      this.title = this.$vuetify.lang.t('$vuetify.home.impress.title')
      this.content = this.$vuetify.lang.t('$vuetify.home.impress.mdContent')

      this._toggleMDDialog()
    },
    showContact() {
      this.title = this.$vuetify.lang.t('$vuetify.home.contact.title')
      this.content = this.$vuetify.lang.t('$vuetify.home.contact.mdContent')

      this._toggleMDDialog()
    }
  }
}
</script>